<template>
    <div v-if="ready">
        <div class="card">
            <div class="card-header" style='height:73px!important;position:relative'>
                <div class='flex'>
                    <strong>{{ display_title() }}</strong>
                </div>
                <div class="tabs">
                    <div class="flex">
                        <div :class="['leftTab subTabs',{'activeTab':mode=='main'},{'disableTab':mode=='sub'}]" @click="select_mode('main')">
                            <u>Main Committee/Department Setting(s)</u>
                        </div>
                        <div :class="['rightTab subTabs',{'activeTab':mode=='sub'},{'disableTab':mode=='main'}]" @click="select_mode('sub')">
                            <u>Sub-Department Setting(s)</u>
                        </div>
                    </div>
                </div>              
            </div>
            <div class="card-body card-block" style='padding:0 0 0 0'>
                <!--Main Dept-->
                <div v-show="mode=='main'" :class="['bodyWrapper',{'activeTab':mode=='main'}]">
                    <label class='col-6'>Search Department</label>
                    <div class="form-group col-12" style="display:flex;flex-direction:row">         
                        <input type="text" class="form-control col-6" v-model="loc_key" placeholder="Search" @keyup="searchloc"/> 
                    </div>           
                    <div class="form-group col-12" style="display:flex;flex-direction:row"> 
                        <div class='col-6' style='padding-left:0;padding-right:0'>
                            <label>Department Name</label>
                            <input type="text" class="form-control" v-model="new_dept" placeholder="Department Full Name"/>
                        </div>
                        <div style="margin-left:auto;padding-right:0" class='col-6'>
                            <label class='col-8'>Department Code</label>
                            <div class='col-12' style="display:flex;flex-direction:row;padding-right:0">
                                <input type="text" class="form-control col-8" v-model="new_dept_id" placeholder="Department ID"/> 
                                <div class='col-2'></div>
                                <button class="form-control col-2" v-on:click.prevent="add_new($event)">Insert</button> 
                            </div>                                     
                        </div>                                           
                    </div>
                    <div class='col-12'>
                        <v-data-table
                        :headers="headers"
                        :items="dept"
                        class="elevation-1"
                        :rows-per-page-items="10"
                        >
                        <template v-slot:items="props">
                            <tr style='cursor:pointer' @click="remove(props.item)">
                                <td>Remove</td>
                                <td>{{ props.item.full_dept }}</td>
                                <td>{{ props.item.dept_id }}</td>
                            </tr>
                        <template v-slot:pageText="props">
                            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                        </template>        
                        </template>
                        </v-data-table><br/>
                    </div>
                    <!--member--> 
                </div>
                <!--Main Dept-->
                <!-------------------------------------------------------------------------->
                <!--Sub Dept-->
                <div v-show="mode=='sub'" :class="['bodyWrapper',{'activeTab':mode=='sub'}]">
                    <div class="form-group col-12 flex">
                        <div class="col-6" style='padding-left:0;padding-right:0'>
                            <label>Main Department</label>
                            <select class='form-control select' v-model="maindept" @change="fetch_sub">
                                <option v-for="sdep in dept" :value="sdep.dept_id">{{ sdep.full_dept }}</option>
                            </select>
                        </div>
                    </div>
                    <div class="form-group col-12 flex"> 
                        <div class='col-6' style='padding-left:0;padding-right:0'>
                            <label>Sub-Department Name</label>
                            <input type="text" class="form-control" v-model="new_subdept" placeholder="Sub-Department Full Name"/>
                        </div>
                        <div style="margin-left:auto;padding-right:0" class='col-6'>
                            <label class='col-8'>Sub-Department Code</label>
                            <div class='col-12 flex' style="padding-right:0">
                                <input type="text" class="form-control col-8" v-model="new_subdept_id" placeholder="Sub-Department ID"/> 
                                <div class='col-2'></div>
                                <button class="form-control col-2" v-on:click.prevent="add_new_sub($event)">Insert</button> 
                            </div>                                     
                        </div>                                           
                    </div>
                    <div class='col-12'>
                        <v-data-table
                            :headers="headers_sub"
                            :items="subdept_list"
                            class="elevation-1"
                            :rows-per-page-items="10"
                        >
                        <template v-slot:items="props">
                            <tr style='cursor:pointer' @click="remove_sub(props.item)">
                                <td>Remove</td>
                                <td>{{ props.item.subdept_full }}</td>
                                <td>{{ props.item.subdept }}</td>
                            </tr>
                        <template v-slot:pageText="props">
                            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                        </template>        
                        </template>
                        </v-data-table><br/>
                    </div>
                </div>
                <!--Sub Dept-->
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return { 
            ready:false,
            mode:'main',
            maindept:'',
            loc_data:[],
            headers: [
                { text: 'Action',value :'id',sortable: false},
                { text: 'Department',value:'full_dept',sortable: false},
                { text: 'Department Code',value:'dept_id',sortable: false},
            ], 
            headers_sub: [
                { text: 'Action',value :'id',sortable: false},
                { text: 'Sub-Department',value:'full_dept',sortable: false},
                { text: 'Sub-Department Code',value:'dept_id',sortable: false},
            ], 
            loc_key:'',
            new_dept:'',
            new_dept_id:'',
            new_subdept:'',
            new_subdept_id:'',
            dept:[],
            subdept_list:[],
            user_check:'',
        }
    },
    async created(){
           await axios.get('Procurement/shared_backend/check_user_setup.php')
                .then(
                    (response)=>{
                        this.user_check = response.data[0].signed;
                        var perm = response.data[0];
                        if(!(this.user_check=='set')){
                            this.$router.push('../user_setup');
                        }
                        if(perm.proc_level<4){
                            this.$alert("No permission").then(
                                ()=>{
                                    this.$router.go(-1);
                                }
                            )
                        }
                    }
                )
            await this.fetch_dept();
            await this.fetch_sub();
            this.ready = true;
    },
    methods:{
        async fetch_dept(){
            await axios.get('Procurement/shared_backend/fetch_dept.php')
            .then(
                (response)=>{
                    if(response.data!='empty'){
                        this.dept = response.data;
                        this.maindept = response.data[0].dept_id;
                        this.dept.id = response.data.id;
                        this.dept.full_dep= response.data.full_dept;
                        this.dept.dept_id = response.data.dept_id;
                    } else {
                        this.dept=[];
                    }
                }
            )
        },
        select_mode(mode){
            this.mode = mode;
        },
        display_title(){
            if(this.mode=='main'){
                return 'Department Setting(s)';
            }
            if(this.mode=='sub'){
                return 'Sub-Department Setting(s)';
            }
        },
        fetch_sub(){
            var formData = new FormData();
            formData.append('maindept',this.maindept);
            axios.post("Procurement/shared_backend/fetch_sub_dept.php",formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.subdept_list = response.data;
                        } else {
                            this.subdept_list = [];
                        }
                    }
                )
        },
        remove(target){
            var formData = new FormData();
            this.$confirm("Confirm deleting department/committee :"+target.full_dept+' ?')
            .then(
                ()=>{
                    formData.append('dept_id',target.dept_id);
                    formData.append('id',target.id);
                    axios.post('Procurement/admin/backend/remove_department.php',formData)
                    .then(
                        (response)=>{
                            if(response.data=='OK'){
                                this.$alert("Successfully deleted "+target.dept_id).then(
                                    ()=>{
                                        this.fetch_dept();
                                    }   
                                )                              
                            } else {
                                this.$alert(response.data);
                            }                          
                        }
                    )
                }
            )     
        },
        searchloc(){
            var formData = new FormData();
            var key = this.loc_key;
            formData.append('code',key);
            axios.post('Procurement/admin/backend/search_department.php',formData)
            .then(
                (response)=>{
                    this.dept = response.data;
                    this.dept.id = response.data.id;
                    this.dept.full_dep= response.data.full_dept;
                    this.dept.dept_id = response.data.dept_id;
                }
            )
        },
        add_new(){
            var formData = new FormData();
            var dept = this.new_dept; 
            var deptid = this.new_dept_id;
            formData.append('full',dept);
            formData.append('deptid',deptid);
            axios.post('Procurement/admin/backend/new_dept.php',formData)
            .then(
                (response)=>{
                    this.dept = response.data;
                    this.dept.id = response.data.id;
                    this.dept.full_dep= response.data.full_dept;
                    this.dept.dept_id = response.data.dept_id;
                    this.new_dept = '';
                    this.new_dept_id='';
                }
            )
        },
        remove_sub(target){
            var formData = new FormData();
            this.$confirm("Confirm deleting department/committee :"+target.subdept_full+' ?')
            .then(
                ()=>{
                    formData.append('id',target.id);
                    formData.append('dept_id',this.maindept);
                    formData.append('sub_dept_id',target.subdept);
                    axios.post('Procurement/admin/backend/remove_subdepartment.php',formData)
                    .then(
                        (response)=>{
                            if(response.data=="OK"){
                                this.$alert("Successfully removed "+target.subdept_full).then(
                                    ()=>{
                                        this.fetch_sub();
                                    }
                                )
                            } else {
                                this.$alert(response.data);
                            }                   
                        }
                    )
                }
            )     
        },
        add_new_sub(){
            var formData = new FormData();
            formData.append('maindept',this.maindept);
            formData.append('full',this.new_subdept);
            formData.append('deptid',this.new_subdept_id);
            axios.post('Procurement/admin/backend/new_subdept.php',formData)
            .then(
                (response)=>{
                    if(response.data=='OK'){
                        this.$alert("Successfully inserted sub-department for "+this.maindept+' :'+this.new_subdept_id).then(
                            ()=>{
                                this.new_subdept = '';
                                this.new_subdept_id='';
                                this.fetch_sub();
                            }
                        )
                    } else {
                        this.$alert(response.data).then(
                            ()=>{
                                this.new_subdept = '';
                                this.new_subdept_id='';
                            }
                        )
                    }
                }
            )
        }
    }
}
</script>
<style scoped>
.flex{
    display:flex;
    flex-direction:row
}
.tabs{
    position:absolute;
    bottom:0;
    height:40px;
    left:0;
    right:0;
}
.leftTab{
    text-align:left;
    padding-left:20px;
    left:0;
    right:50%;
    border-top-right-radius: 35px;
}
.rightTab{
    text-align:right;
    padding-right:20px;
    right:0;
    left:50%;
    border-top-left-radius: 35px;
}
.subTabs{
    font-weight: bold;
    padding-top:5px;
    position:absolute;
    bottom:-2px!important;
    background-color:#FFFFFF;
    height:35px;
    opacity:0.4;
    cursor:pointer;
    transition: 0.3s;
    transition-property: opacity;
}
.activeTab{
    opacity:1!important;
    z-index: 10;
}
.leftTab.activeTab{
    right:45%;
}
.rightTab.activeTab{
    left:45%;
}
.bodyWrapper{
    margin-top:10px;
    padding-left:8px;
    padding-right:8px;
    opacity:0;
    transition: 0.3s;
}
.disableTab:hover{
    opacity: 0.65;
}
</style>


